import {useState, useEffect} from "react";

import './slider.scss'

import slidefotos from './slideimg.json'

function Slider(){


    const [slideImg, setSlideImg] = useState(slidefotos[0].id);
    const [slideImgn, setSlideImgn] = useState(slidefotos[0].src);

    useEffect(() => {

        const timer = setInterval(() => {
            if(slideImg === 1){
                setSlideImg(2);
                setSlideImgn(slidefotos[1].src);
            }
            else if(slideImg === 2){
                setSlideImg(3);
                setSlideImgn(slidefotos[2].src);
            }
            else{
                setSlideImg(1);
                setSlideImgn(slidefotos[0].src);
            }
        }, 5000)

        return () => clearInterval(timer)

    });

    return(
        <div className='slider_container'>
            <div className='cont_img'>
                <img src={require(`../img/productos${slideImgn}`).default} className='slide_fotos' alt={slideImgn} ></img>
            </div>
            {/* <Carousel showArrows={true} onChange={onChange} onClickItem={onClickItem} onClickThumb={onClickThumb}>
                <div>
                    <img src={require(`../img/slider/ARANDANOS IMAGEN.png`).default} className='flex_fotos' alt='ARANDANOS IMAGEN.png' ></img>
                    <p className="legend">ARANDANOS 1</p>
                </div>
                <div>
                    <img src={require(`../img/slider/ARANDANOS IMAGEN 2.png`).default} className='flex_fotos' alt='ARANDANOS IMAGEN 2.png' ></img>
                    <p className="legend">ARANDANOS 2</p>
                </div>
                <div>   
                    <img src={require(`../img/slider/ARANDANOS IMAGEN 3.png`).default} className='flex_fotos' alt='ARANDANOS IMAGEN 3.png' ></img>
                    <p className="legend">ARANDANOS 3</p>
                </div>
            </Carousel>    */}           
            
        </div>
    )
}

export default Slider;